import { useEffect } from 'react';
import { useLocation } from 'react-router-dom'; 
import { loginWithShopify, installShopify } from '../../../actions/loginActions'; 
import { useDispatch } from 'react-redux';
import { Loader } from "../../Layout/Main/CommonComponents/Loader";


const useSearchParams = () => {
    const location = useLocation();
    const searchParams = new URLSearchParams(location.search);
    return {
        app_load_id: searchParams.get('app_load_id'),
        code: searchParams.get('code'),
        embedded: searchParams.get('embedded'),
        hmac: searchParams.get('hmac'),
        host: searchParams.get('host'),
        id_token: searchParams.get('id_token'),
        locale: searchParams.get('locale'),
        session: searchParams.get('session'),
        shop: searchParams.get('shop'),
        timestamp: searchParams.get('timestamp'),
    };
};

const removeNullParams = (obj) => {
    return Object.fromEntries(
        Object.entries(obj).filter(([_, value]) => value !== null && value !== undefined)
    );
};

const ShopifyInstall = ({ history, isInstall }) => {
    const params = useSearchParams();
    const cleanParams = removeNullParams(params);
    const dispatch = useDispatch();

    useEffect(() => {
        if (isInstall) {
            if (cleanParams.embedded) {
                dispatch(loginWithShopify(cleanParams, history))
            } else {
                installShopify(cleanParams.shop);
            }
        } else {
            dispatch(loginWithShopify(cleanParams, history))
        }
    }, []);

    return <div className='h-100vh w-100 f-center'><Loader /></div>;
};

export default ShopifyInstall